@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
};

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content
  }
};

@mixin onTablet {
  @media (max-width: 1024px) {
    @content;
  }
}

@mixin onMobile {
  @media (max-width: 500px) {
    @content
  }
};

@mixin nonMobile {
  @media (min-width: 500px) {
    @content
  }
}

@mixin top-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $menuMargins;
};
